.notification {
  display: none;
}
.warning {
  visibility: collapse;
}
.notification a {
  text-decoration: underline;
  color: blue;
}

@media (max-width: 768px) {
  .notification {
    display: flex;
    flex-direction: column;
    overflow: hidden;

    justify-content: center;
    align-items: center;
    background-color: rgb(255, 106, 106);

    color: black;
    font-family: monospace;
    margin-top: 70px;
    padding: 30px;
    text-align: center;
  }
  article {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    overflow-y: hidden;
    flex: 1;

    align-items: center;
    /* font-size: 22px; */
  }
  .warning {
    visibility: visible;
    font-size: 72px;
  }

  .hero,
  .header,
  .features,
  .sectionone,
  .works,
  .footer,
  .services,
  .contact,
  footer,
  .dynamic_island__container {
    visibility: hidden;
    
  }
  .notification img {
    width: 200px;
    height: 400px;
  }
}
