@font-face {
  font-family: Jazmin Alt Regular;
  src: url('./assets/Jazmin\ Alt\ Regular.otf') format('opentype');
}

.services__h1 {
  background-color: rgb(46, 229, 157, 0.5);
  padding: 10px;
  padding-block: 10px;
  border-radius: 25px;
  border: 1px solid white;
}

.services__container {
  background: url(https://assets.website-files.com/6202a5c7263f5aa6a63ecae2/6267cef76c1c0f3e1d5864b6_neon-sign-bg.webp);

  background-size: cover;
  border-radius: 25px;
  object-fit: scale-down;
  display: flex;
  padding: 16px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 80px;
  text-align: center;
}

.services__box {
  margin-top: 10px;
  display: flex;
  gap: 60px;
  text-transform: uppercase;
  font-family: 'Jazmin Alt Regular';
  font-size: 1.5rem;
}

.extra-dots {
  user-select: none;
  color: #2ee59d;
  letter-spacing: 2px;
  font-size: 3rem;
}

.service__name {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
}

.service__name:hover,
.service__name:focus,
.service__name:active {
  -webkit-transform: scale(1.1) rotate(4deg);
  transform: scale(1.1) rotate(4deg);
  color: #2ee59d;
  cursor: pointer;
}
