.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* margin-top: -100px; */
}
.stand_out {
  color: #2ee59d;
  position: relative;
}

.arrow {
  position: absolute;
  left: 0;
  bottom: -10px;
}

.arrow_arc {
  position: absolute;
  width: 80px;
  rotate: 280deg;
  top: 27px;
  right: 0;
  z-index: -10;
}

.hero__container {
  text-align: center;
  width: 968px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin-top: 100px;
  margin-bottom: 60px;
}

.hero__container p {
  margin-top: 20px;
}

.hero__left {
  font-size: 1.8rem;
  font-family: 'poppins';
  margin-right: 20px;
  line-height: 1.2;
}
.hero__right {
  font-family: 'poppins';
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero__buttons {
  margin-block: 20px;
  display: flex;
  gap: 20px;
  align-self: center;
}

.hero__button {
  background-color: rgb(249, 249, 249);
  color: rgb(18, 18, 18);
  font-family: poppins;
  font-size: 14px;
  padding-inline: 14px;
  padding-block: 10px;
  border-radius: 100px;
  cursor: pointer;
  transition: all 0.3s ease 0s;

  border: none;
}

.button-outline {
  background-color: transparent;
  border: 1px solid rgb(249, 249, 249);
  color: rgb(249, 249, 249);
}
.hero__button:hover {
  /* color: black;
  background-color: white;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1); */
  background-color: #2ee59d;
  box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
  color: #fff;
  transform: translateY(-7px);
  transition: transform 0.3s ease-in-out;
}

.button-outline:hover {
  background-color: transparent;
  color: #2ee59d;
  box-shadow: 0px 15px 20px rgba(255, 255, 255, 0.4);
}

.hero__section {
  position: relative;
}
.hero__img {
  /* width: 600px */
  /* background-color: white; */
  margin-top: 50px;
  width: 1080px;
  height: 600px;
  object-fit: cover;
  -webkit-mask-image: linear-gradient(
    to bottom,
    rgb(255, 255, 255),
    rgba(255, 255, 255, 0)
  );
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  border-radius: 50px 50px 0 0;
  overflow: hidden;
  color: white;
}
.hero__text {
  display: inline;
  font-family: 'poppins';
  font-weight: 100;
  font-size: 1rem;
  border-radius: 20px;
  padding-inline: 20px;
  padding-block: 20px;
  background-color: rgba(255, 255, 255, 0.5);
  /* addding abosoulte postining fromm here  */
  position: absolute;
  top: 10px;
  left: 50px;
  border: 1.5px solid rgb(46, 229, 157, 0.5);
}

.hero__span {
  font-size: 1.5rem;
  color: #2ee59d;
}

.hero__circle {
  position: absolute;
  top: 85px;
  right: 35px;
  padding: 20px;
  border-radius: 100%;
  background-color: #2ee59d;
  border: 2px solid white;
  font-family: 'poppins';
  user-select: none;
  font-size: 1.5rem;
  animation: hero__rotate 2s alternate infinite ease-in-out;
}

@keyframes hero__rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.hero__btmInfo {
  position: absolute;
  width: 900px;
  height: fit-content;
  background-color: white;
  color: #000;
  border-radius: 25px;
  padding: 14px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  right: 75px;
  bottom: -30px;
  box-shadow: 0px 15px 20px rgba(1, 1, 1, 0.4);
}

.hero__btmInfo_flexbox {
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  height: 110px;
  background-color: rgb(232, 232, 232);
  border-radius: 12px;
  padding: 8px;
}

.hero__btmInfo h3 {
  margin-bottom: 10px;
}
