.features__container {
  display: flex;
  /* width: 1000px; */
  height: 100px;
  gap: 20px;
  padding: 20px;

  /* background-color: greenyellow; */
  border: none;
}
.features__card {
  flex: 1;
  width: fit-content;
  border: 1px solid darkgrey;
  border-style: none solid none none;
  padding: 3px;
}

.hero__icons {
  width: 20px;
  color: aquamarine;
}
.features__container h4 {
  font-family: 'poppins';
}

.features__container p {
  color: rgb(96, 90, 90);
  font-size: 14px;
  word-wrap: break-word;
}

.btn {
  margin-bottom: 10px;
}
