.contact__container {
  max-width: 1075px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact__container > h1 {
  font-family: 'poppins';
  background-color: #2ee59d;

  border-radius: 8px;
  margin: 8px;
  padding: 8px;
  border: 1px;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
}

.contact__container > h1:before {
  position: absolute;
  z-index: -1;
  content: '';
  left: calc(50% - 10px);
  bottom: 0;
  border-style: solid;
  border-width: 10px 10px 0 10px;
  border-color: #2ee59d transparent transparent transparent;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
}
.contact__container > h1:hover,
.contact__container > h1:focus,
.contact__container > h1:active {
  -webkit-transform: translateY(-10px);
  transform: translateY(-10px);
}
.contact__container > h1:hover:before,
.contact__container > h1:focus:before,
.contact__container > h1:active:before {
  -webkit-transform: translateY(10px);
  transform: translateY(10px);
}

.contact__box {
  margin-top: 20px;
  display: flex;
  padding: 40px;
  margin-bottom: 50px;
  border: 1px solid rgb(169, 169, 169,0.3);
  border-style: none none solid none;
}
