footer {
  background-color: white;
  display: flex;
  flex-direction: column;
  color: #7a7a7a;
  justify-content: center;
  align-items: center;

  border-radius: 25px 25px 0px 0px;
}
.footer {
  display: flex;

  height: 200px;
  color: black;

  display: flex;
  justify-content: space-around;
  padding-top: 20px;
  padding-inline: 40px;
  flex: repeat(3, 1fr);
  flex-wrap: wrap;
}

.footer h4 {
  font-size: 1.5rem;
  font-family: 'poppins';
  margin-bottom: 1rem;
  padding-left: 0;
}

.footer__li {
  text-decoration: none;
  list-style: none;
  color: #7a7a7a;
  font-family: 'roboto';
  font-size: 18px;
  margin-bottom: 6px;
  cursor: pointer;
}

.footer__li :hover {
  color: #2ee59d;
}

.footer a {
  color: #7a7a7a;
}

.footer__row {
  display: flex;
  flex: 1 1 0;
  flex-direction: column;
  justify-content: space-evenly;
}

.outline {
  border: 1px solid black;
  background-color: #e4e4e4;
}

.footer_call {
  color: #7a7a7a;
  font-size: 18px;
}

.footer_icons {
  width: 14px;
}
.rights {
  margin-bottom: 10px;
  font-size: 10px;
}
