* {
  margin: 0;
  padding: 0;
}

/* header,
ul,
li,
p,
img,
body,
div {
  border: 2.2px solid #5a13ff;
} */
::selection {
  color: black;
  background: #2ee59d;
  padding: 2px;
  border-radius: 50px;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(0, 0, 0);

  color: white;
}
.header {
  position: fixed;
  z-index: 100;
  min-width: 1080px;
  /* max-width: max-content; */
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-block: 10px;
  background-color: rgb(0, 0, 0);
  padding-top: 14px;
  /* width: 1500px; */
  /* background-color: black; */
  text-align: center;
  user-select: none;
  margin-left: 9px;
  /* border-bottom: rgb(255, 255, 255) 1px solid; */
  box-shadow: 0px 1px 18px rgb(255, 255, 255, 0.3);
}

.header__img {
  height: 26px;
  /* filter: invert(100%); */
}

header ul {
  display: flex;
  gap: 20px;
}

header li {
  text-decoration: none;
  list-style: none;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 1.5px;
  font-family: 'poppins';
  font-size: 14px;
  cursor: pointer;
}

header li::after {
  content: '';
  display: block;
  /* width: 100%; */
  transform: scaleX(0);
  font-weight: bold;
  height: 3px;
  bottom: 0;
  left: 0;
  border-radius: 10px;
  background-color: #2ee59d;
  transform-origin: center bottom;
  transition: transform 0.25s ease-in-out;
}
header li:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
  font-weight: bolder;
}
header li:active::after {
  font-weight: bolder;
  background-color: #a22ee5;
}

.icons {
  width: 18px;
  height: 18px;
  padding: 0;
  margin: 0;
}
.buttons {
  background-color: rgb(249, 249, 249);
  color: rgb(18, 18, 18);
  font-family: poppins;
  font-size: 14px;
  padding-inline: 14px;
  padding-block: 10px;
  border-radius: 100px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  scale: 0.8;
}

.buttons:hover {
  /* color: black;
  background-color: white;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1); */
  background-color: #2ee59d;
  box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
  color: #fff;
  transform: translateY(-7px);
  transition: transform 0.3s ease-in-out;
}

.header__button {
  display: flex;
  align-items: center;
  gap: 12px;
}

.header__callouts {
  display: flex;
  align-items: center;
}

.header__callouts p {
  color: rgb(185, 185, 185);
  font-family: 'poppins';
  font-size: 12px;
  user-select: all;
}

.try-it {
  border: 1px solid #5a13ff;
  padding: 4px;
}

/* Mobile Screen */

@media screen and (max-width: 768px) {
  .header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    padding-top: 14px;
    gap: 40px;
    text-align: center;
    user-select: none;
  }
  .header__img {
    height: 56px;
    filter: invert(100%);
  }
  header ul {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }
  header li {
    text-decoration: none;
    list-style: none;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 1.5px;
    font-family: 'poppins';
    font-size: 24px;
    cursor: pointer;
  }
  header li::after {
    content: '';
    display: block;
    /* width: 100%; */
    transform: scaleX(0);
    font-weight: bold;
    height: 3px;
    bottom: 0;
    left: 0;
    border-radius: 10px;
    background-color: #2ee59d;
    transform-origin: center bottom;
    transition: transform 0.25s ease-in-out;
  }
  header li:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
    font-weight: bolder;
  }
  header li:active::after {
    font-weight: bolder;
    background-color: #a22ee5;
  }

  .header__callouts {
    visibility: hidden;
  }
}
