.dynamic_island__container {
  position: relative;

  top: 620px;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}
img {
  padding: 0%;
  margin: 0;
}

.social__container {
  position: fixed;
  bottom: 10px;
  right: 40%;
  background: rgb(46, 229, 157);
  background: -moz-linear-gradient(
    73deg,
    rgba(46, 229, 157, 1) 0%,
    rgba(30, 153, 138, 1) 100%
  );
  background: -webkit-linear-gradient(
    73deg,
    rgba(46, 229, 157, 1) 0%,
    rgba(30, 153, 138, 1) 100%
  );
  background: linear-gradient(
    73deg,
    rgba(46, 229, 157, 1) 0%,
    rgba(30, 153, 138, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#2ee59d",endColorstr="#1e998a",GradientType=1);

  color: rgb(255, 255, 255);
  font-family: poppins;
  font-size: 18px;
  padding-inline: 22px;
  padding-top: 4px;
  border-radius: 100px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  scale: 0.8;
  display: flex;
  gap: 22px;
}

.social__container:hover {
  /* color: black;
  background-color: white;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1); */
  /* background-color: rgb(249, 249, 249); */
  border: 1px solid white;
  background: rgb(0, 0, 0);
  background: -moz-linear-gradient(
    73deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(40, 40, 40, 1) 100%
  );
  background: -webkit-linear-gradient(
    73deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(40, 40, 40, 1) 100%
  );
  background: linear-gradient(
    73deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(40, 40, 40, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#000000",endColorstr="#282828",GradientType=1);
  color: rgb(18, 18, 18);
  box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);

  transform: translateY(-7px);
  transition: transform 0.3s ease-in-out;
}
a {
  padding: 0;
  margin: 0;
}

.social_media {
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease 0s;
  border-radius: 100px;
  padding: 4px;
}
.social_media:hover {
  /* background-color: aliceblue; */
  transform: translateY(-7px);
  transition: transform 0.3s ease-in-out;
}
