.works__container {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background: #fff; */
  color: #fff;
  padding: 30px 0;
}

.works__container a {
  color: #000;
}

.cards {
  display: flex;

  align-items: center;
  position: relative;
}

.card {
  width: 300px;
  height: 300px;
  margin: 10px;
  border-radius: 14px;
  object-fit: cover;
  justify-content: center;
  overflow: hidden;
  border: 2px solid rgb(46, 229, 157, 0.5);
}
.card h1 {
  font-family: 'Jazmin Alt Regular';
}

.card p {
  font-family: 'roboto', monospace;
}

.card__img {
  width: 100%;
  height: 100%;
  transition: transform 0.5s;
  object-fit: cover;
}

.card__text {
  border-radius: 10px;
  height: 300px;
  width: 300px;
  position: absolute;
  display: flex;
  flex-direction: space-around;
  align-items: center;
  justify-content: center;
  /* justify-content: space-evenly; */
  /* justify-content: space-between; */

  background: linear-gradient(0deg, rgb(7, 7, 7), rgba(0, 0, 0, 0.5));
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  text-align: center;
  user-select: none;
}

.card__text:hover,
.card__text:focus,
.card__text:active {
  opacity: 1;
}

.card__textContent {
  /* padding-left: 20px; */
  display: flex;

  gap: 20px;
  flex-direction: column;
  justify-content: center;
}

.goItem {
  /* width: 150px; */
  cursor: pointer;
  /* width: max-content; */

  background-color: rgb(249, 249, 249);
  color: rgb(18, 18, 18);
  font-family: poppins;
  font-size: 14px;

  padding-block: 10px;
  border-radius: 100px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  align-items: center;
  scale: 0.8;
}
.goItem:hover,
.goItem:active {
  background-color: #2ee59d;
  box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
  color: #fff;
  transform: translateY(-7px);
  transition: transform 0.3s ease-in-out;
}
